  import goodsSwiper from '../../components/swiper.vue'
  import goodsFooter from './components/footer.vue'
  import goodsSelected from './components/selected.vue'
  import CubePage from '../../components/cube-page.vue'
  export default {
    name: 'GoodsIndex',
    data (){
      return {
        cartNum: 0,
        maskShow: false,
        selected: false,
        goods: {},//商品信息
        lists: [],//商品参数 型号/颜色/数量
        banner: [],//商品轮播图
        isBall: false,//加入购物车动画元素
        collect: 0, //是否收藏
        isShare: false,
        number: 1,
      }
    },
    components: {
      goodsSwiper,
      goodsFooter,
      goodsSelected,
      CubePage
    },
    methods: {
      reduce(){
        if(this.number<=1){
          this.showToastTxtOnly('受不了了，宝贝不能再减少了哦')
          return false
        }else{
          this.number -=1;
        }
      },
      //加法
      plus(stock){
        if(this.number>=stock) {
          this.showToastTxtOnly('已经没有更多的库存了，下次再来吧')
          return false
        }else{
          this.number +=1
        }
      },
      //数量监听限制
      handleNumChange(stock){
        if(this.number>=stock){
          this.showToastTxtOnly('超出范围')
          this.number = stock
        }
      },
      //分享
      shareClick(){
        this.isShare = true
      },
      //分享关闭
      changeCloseShare(){
        this.isShare = false
      },
      showToastTxtOnly(text) {
        this.toast = this.$createToast({
          txt: text,
          type: 'txt'
        })
        this.toast.show()
      },
      //商品收藏新增
      collectionClick(id){
        this.http.post(this.ports.goods.collection,{goods_id: id}, res =>{
          if(res.success){
            let data = res.data
            this.collect = true
          }else{
            this.showToastTxtOnly(res.msg)
          }
        })
      },
      //取消收藏
      cancelClick(id){
        this.http.delete(this.ports.goods.collection+'/'+id, res =>{
          if(res.success){
            this.collect = false
          }else{
            this.showToastTxtOnly(res.msg)
          }
        })
      },
      //加入购物车动画
      beforeEnter(el){
        el.style.transform = "translate(0, 0)"
      },
      enter(el, done){
        // el.offsetWidth 强制html渲染动画
        // el.offsetWidth 这句话如何不写就不会有动画效果直接渲染的
        el.offsetWidth;
        el.style.transform = "translate(-70px, 68vh) scale(0.01)";
        el.style.transition = "all 1s ease";
//        console.log(done);
        done()
      },
      afterEnter(el){
//        this.flag = !this.flag
      },
      //获取列表数据
      getIndexData(){
        let id = this.$route.params.id
        this.http.get(this.ports.integral.points+'/'+id, res =>{
          this.$store.commit('changeLoading',false)
          if(res.success){
            let data = res.data
            this.goods = data
            this.lists = data.lists
            this.banner = data.image
            this.collect = data.collect
            console.log(this.goods)
          }else{
            this.showToastTxtOnly(res.msg)
          }
        })
      },
      //弹出规格选择
      confirmClick(){
        this.isBall = false
        this.maskShow = true
        const that = this
        setTimeout( ()=>{
          that.selected = true
        },200)
      },
      //加入购物车
      changeAddCart(val,type){
        let that = this
        this.cartNum = val
        this.selected = false
        if(!this.selected){
          setTimeout(()=>{
            this.maskShow = false
            this.isBall = true
            if(type){
              that.$store.commit('changeCartNum',parseInt(this.$store.state.cartNum)+0)
            }else{
              that.$store.commit('changeCartNum',parseInt(this.$store.state.cartNum)+val)

            }
          },500)
        }
      },
      //关闭规格选择
      changeSelected(val) {
        this.selected = false
        if(!this.selected){
          setTimeout(()=>{
            this.maskShow = false
          },500)
        }
      },
      exchangeClick(id){
        this.$router.push({path: '/integralOrder/orders',query:{
          goods_id: id,
          count: this.number
        }})
      }
    },
    mounted (){
      this.getIndexData()
    }
  }



